.tasks {
    list-style: none;
    margin: 0;
    padding: 0;
}

.tasks__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ddd;
}

.tasks__item--hidden {
    display: none;
}

.tasks__content--done {
    text-decoration: line-through;
}

.tasks__button {
    border: none;
    cursor: pointer;
    color: white;
    width: 30px;
    height: 30px;
    padding: 0px;
    margin: 5px;
    flex-shrink: 0;
}

.tasks__button--toggleDone {
    background: rgb(26, 122, 32);
    transition: 1s;
}

.tasks__button--toggleDone:hover {
    background: hsl(124, 65%, 40%);
}

.tasks__button--toggleDone:active {
    border: 2px solid black;
    border-radius: 2px;
}

.tasks__button--remove {
    background: hsl(350, 80%, 45%);
}

.tasks__button--remove:hover {
    background: hsl(350, 80%, 60%);
}

.tasks__button--done:active {
    background: hsl(350, 80%, 55%);
}